import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			sid:0,
			salesOrder:'',
			source: 0,
			addressList: [],
			scrollHeight: 100,
			tabScrollTop: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
			keyword:'',
			type:0,
		};
	},
	
	onLoad(options){
		console.log('页面',options);
		
		getApp().user_api(true);
		this.calcSize();
		this.getProductList();
		
	},
	onShow(){
	},
	onPageScroll(e){
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//加载更多
	onReachBottom(){
		// this.loadData();
	},
	methods: {
		
		getProductList(){
			let self = this;
			// console.log('排序',value);
			// 117.107846,36.693767 ligao 
			
			var path = '/gzh/merchant_api/merchant_list'
			var data = {
				page:self.page,
				limit:self.limit
			};
			
			getApp().http(path, data, function(res) {
				console.log('详情',res);
				uni.hideLoading();
				
				let list = res;
				if (list.length > 0) {
					console.log('总条数',list.length)
					
					self.goodsList = self.goodsList.concat(list);
					self.status = list.length >= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
					}
				} else {
					self.status = "";
				}
			})
		},
		
		
		phone(item){
			console.log('kefu ',item)
			let customer_service = item.customer_service
			uni.showModal({
				title: '是否拨打客服电话',
				content: customer_service,
				success: (md) => {
					console.log('sssss')
					if(md.confirm == true){
						uni.makePhoneCall({
						    phoneNumber: customer_service
						});
					}
				}
			})
		},
		
		
		//筛选点击
		tabClick(index){
			let self = this;
			console.log('点击',index)
			
			self.filterIndex = index;
			
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			
			uni.showLoading({
				title: '正在加载'
			})
			
			if(self.filterIndex == 0){
				console.log('优惠券')
				self.page = 1;
				self.goodsList = []
				self.type = 0
				self.getProductList()
			}else if(self.filterIndex == 1){
				self.page = 1;
				self.goodsList = []
				self.type = 1
				self.getProductList()
				console.log('我的')
			}
		},
		
	
		
		indexUrl(icon){
			let http = icon.indexOf('http')
			if(http == -1){
				return this.webUrl +'/'+icon
			}else{
				return icon;
			}
		},
		
		clickD(item){
			console.log('跳转',item);
			uni.navigateTo({
				// url: "/pages/product/product?id="+item.pid
				url: "/pages/seller/seller_product?id="+item.id
			})
		},
		
		// 到底触发
		scroAddr(){
			let self = this;
			if(self.status){
				self.getProductList();
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			var self = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					let info = uni.createSelectorQuery().select(".content");
					self.scrollHeight = res.windowHeight
					// self.scrollHeight = 200
					// console.log(info);
					// console.log('啊啊啊啊',res);
				}
			});
		},
		
	},
}
